import React from 'react'
import { useState } from 'react'
import { ThemeProvider, makeStyles, Typography, Container, TextField, Grid, Link, Box, Button, createMuiTheme } from '@material-ui/core';
import { useFormik } from 'formik'
import { Auth } from '../../validations'
import { AuthService } from '../../services'
import localForage from 'localforage';
import { Switch } from "react-router-dom"
import PrivateRoutes from '../../PrivateRoutes'
import Dashboard from '../Dashboard'

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '950px', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
        paddingLeft: '590px',
        color: 'red'

    },
    submit: {
        margin: theme.spacing(3, 0, 2),
        width: '50%',
        alignItems: 'center',
        backgroundColor: '#4188FF'

    },

    heading: {
        alignItems: 'center',
        width: '850px', // Fix IE 11 issue.
        paddingLeft: '590px'
    },
    forgetpass: {
        paddingLeft: '240px',
        textDecorationColor: 'black'
    },
    btnsignin: {
        paddingLeft: '140px'
    },
    MuiTypographycolorPrimary: {
        alignItems: 'center',
        paddingLeft: '90px',
        color: '#4188FF !important',
        textDecorationColor: '#4188FF !important'

    },
    image: {
        paddingRight: '900px'
    },
    email: {
        outlineColor: 'red'
    },
    multilineColor: {
        // borderColor: 'red',
        // borderWidth: 1,
        // '&:hover': {
        //     borderColor: 'red',
        //     borderWidth: 2
        // },
        borderWidth: "1px",
        borderColor: "#4188FF !important",
        color: '#4188FF'

    }


}));

function Login() {
    const classes = useStyles();

    const initState = {
        email: "",
        password: ""
    }

    const [isLogin, setIsLogin] = useState(false)

    const [ErrorMsg, SetErrorMsg] = useState(null)

    const formSubmit = async (values, action) => {

        const body = {
            email: values.email,
            password: values.password
        }

        AuthService.Login(body)
            .then(res => {
                console.log('Response', res.data);
                const user = res.data
                localForage.setItem("User", res.data)
                setIsLogin(true)
                action.resetForm();

            })
            .catch(err => {
                console.log('Error', err.response);
                SetErrorMsg('error logging in')
            })

    }

    const formik = useFormik({
        initialValues: { ...initState },
        onSubmit: formSubmit,
        validationSchema: Auth.LoginVld
    })

    return (
        <div >
            <div className="">
                <div className="col-md-4 col-sm-4 logo text-center">
                    <img src="images/logo/logo.svg" />
                </div>
                <div className=" offset-md-8">

                </div>
            </div>
            <Container className="d-flex">

                <div className="col-md-6">

                </div>

                <div className=" d-flex flex-column mt-0 p-5 m-5">


                    <Typography component="h2" variant="h5" className="text-center">
                        Sign in Your Account.
                    </Typography>


                    <form className="d-flex flex-column my-4 py-4 p-3 justify-content-center" onSubmit={formik.handleSubmit}>

                        <TextField
                            className="my-4"
                            id="email"
                            name="email"
                            label="Email Address"
                            value={formik.values.email}
                            onChange={formik.handleChange}
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            autoComplete="email"
                            autoFocus
                            onBlur={formik.handleBlur}
                            error={formik.touched.email && formik.errors.email}
                            helperText={formik.touched.email && formik.errors.email ? formik.errors.email : ''}
                            InputProps={{
                                classes: {
                                    input: classes.multilineColor
                                }
                            }}

                        />

                        <TextField
                            id="password"
                            label="Password"
                            value={formik.values.password}
                            onChange={formik.handleChange}
                            required
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            type="password"
                            autoComplete="current-password"
                            onBlur={formik.handleBlur}
                            error={formik.touched.password && formik.errors.password}
                            helperText={formik.touched.password && formik.errors.password ? formik.errors.password : ''}
                            InputProps={{
                                classes: {
                                    input: classes.multilineColor
                                }
                            }}
                        />


                        <Grid item xs className={classes.forgetpass}>
                            <Link href="#" variant="body2" style={{ color: 'black' }} className="ml-4">
                                Forgot password?
                            </Link>
                        </Grid>
                        <div className={classes.btnsignin}>
                            <Button
                                type="submit"
                                // fullWidth
                                variant="contained"
                                color="primary"
                                className={classes.submit}>
                                Sign In
                             </Button>
                        </div>
                        <div className="">{ErrorMsg}</div>
                        <br></br>

                    </form>

                    <Box mt={8} >
                        <Typography variant="body2" color="textSecondary" align="center">
                            {"Don't have an account ?"}
                            <Link color="primary" href="#">
                                Sign Up
                            </Link>
                        </Typography>
                    </Box>
                </div>


            </Container>

        </div>
    )
}

export default Login

