import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import General from '../components/General';
import VerticalTabs from '../components/Integration'


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`horizontal-tabpanel-${index}`}
      aria-labelledby={`horizontal-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `horizontal-tab-${index}`,
    'aria-controls': `horizontal-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
  tabs: {
    borderBottom: `1px solid ${theme.palette.divider}`,
  },
}));

function Setting() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <div>
                <h2
                    className="my-4 ml-5 h"
                    id="keyHeading">
                    Settings
                </h2>
      </div>
      <Tabs
        indicatorColor="primary"
        textColor="primary"
        orientation="horizontal"
        value={value}
        onChange={handleChange}
        aria-label="horizontal tabs example"
        className={classes.tabs}
      >
        <Tab label='General' style={{fontSize:'24px',color:'#002E79',fontFamily:'Poppins',textTransform:'capitalize'}} {...a11yProps(0)} id="setting1"/>
          <br/><br/>
        <Tab label='Integration' className="h" {...a11yProps(1)} id="setting1"  style={{fontSize:'24px',color:'#002E79',fontFamily:'Poppins',textTransform:'capitalize'}}/>
          <br/><br/>        
      </Tabs>
      <TabPanel value={value} index={0}>
        <General />
      </TabPanel>
      <TabPanel value={value} index={3}>
        <VerticalTabs />
      </TabPanel>
    </div>
  );
}


export default Setting