import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Mailchimp from './Mailchimp'
import Mailgun from './Mailgun'



function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
    maxHeight: 224,
    height: 400,
  },
  tabs: {
    maxHeight: '600px',
    height: '450px',
    borderRight: `1px solid ${theme.palette.divider}`,
  },
}));

function VerticalTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <Tabs
        indicatorColor="primary"
        textColor="primary"
        orientation="vertical"
        variant="scrollable"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        className={classes.tabs}
      >
        <Tab icon={<img id="integration1" src="images/mailchimp.png" />} {...a11yProps(0)} id="integration2" />
        <br /><br />
        <Tab icon={<img id="integration3" src="images/mailgun.png" />} {...a11yProps(1)} id="integration2" />
        <br /><br />
        <Tab icon={<img id="integration4" src="images/twilio.png" />} {...a11yProps(2)} id="integration2" />
        <br /><br />
        <Tab icon={<img id="integration5" src="images/silferbot.png" />} {...a11yProps(3)} id="integration2" />
        <br /><br />
        <Tab icon={<img id="integration6" src="images/activecampaign.png" />} {...a11yProps(4)} id="integration2" />
        <br /><br />
        <Tab icon={<img id="integration7" src="images/activechat.png" />} {...a11yProps(5)} id="integration2" />
      </Tabs>

      <TabPanel value={value} index={0}>
        <Mailchimp />
      </TabPanel>
      <TabPanel value={value} index={1} />
      <TabPanel value={value} index={2} />
      <TabPanel value={value} index={3}>
        <Mailgun />
      </TabPanel>
      <TabPanel value={value} index={4} />
      <TabPanel value={value} index={5} />
    </div>
  );
}


export default VerticalTabs